import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import * as actions from "../actions";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = { msg: "" };
  }
  render() {
    return (
      <div className="serveyform">
        <div className="login">
          <span style={{ color: "red" }}>{this.state.msg}</span>
          <form
            onSubmit={this.props.handleSubmit(e => {
              this.props.login(e.email, e.password);
              e.email = "";
              e.password = "";
              if (!this.props.user) {
                this.setState({ msg: "User Name or Password incorrect" });
              }
            })}
          >
            <label> Email</label>
            <Field type="text" name="email" component="input" />
            <label> Password</label>
            <Field type="password" name="password" component="input" />
            <span className="butsubmit">
              <button className="logbut" type="submit">
                Login
              </button>
              <Link to="/forgot" >
                Forgot Password
              </Link>
              <Link to="/register" className="logbut">
                Register
              </Link>
            </span>
          </form>
        </div>
      </div>
    );
  }
}
Login = connect(null, actions)(Login);
export default reduxForm({
  form: "login"
})(Login);



