import { FETCH_USER, LOG_OUT } from "../actions/type";

export const fetchUser = (state = null, action) => {
  // console.log(action.payload);
  switch (action.type) {
    case FETCH_USER:
      return action.payload || false;
    case LOG_OUT:
      return state;
    default:
      return state;
  }
};



