import { FETCH_USER } from "./type";
import axios from "axios";

export const fetchUser = () => {
  return dispatch => {
    axios.get("/api/user").then(res => {
      dispatch({
        type: FETCH_USER,
        payload: res.data
      });
    });
  };
};

export const logout = () => {
  return dispatch => {
    axios.get("/api/logout").then(res => {
      console.log("loggedout");
      // this.props.fetchUser();
      dispatch({
        type: FETCH_USER,
        payload: res.data
      });
    });
  };
};

export const stripe = (token, amount) => {
  console.log(token, amount);
  return dispatch => {
    axios.post("/api/payout", { token, amount }).then(res => {
      console.log("loggedout");
      // this.props.fetchUser();
      dispatch({
        type: FETCH_USER,
        payload: res.data
      });
    });
  };
};
export const login = (email, password) => {
  console.log(email, password);
  return dispatch => {
    axios
      .post("/api/login", { username: email, password: password })
      .then(res => {
      
        // this.props.fetchUser();
        dispatch({
          type: FETCH_USER,
          payload: res.data
        });
      });
  };
};

export const register = (email, password,name) => {

  return dispatch => {
    axios
      .post("/api/register", { email, password,name })
      .then(res => {

        // this.props.fetchUser();
        dispatch({
          type: FETCH_USER,
          payload: res.data
        });
      });
  };
};



