import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import * as actions from "../actions";
import { connect } from "react-redux";
import axios from "axios";

class Forgot extends Component {
  constructor(props) {
    super(props);
    this.state = { msg: "" };
  }

  render() {
    return (
      <div>
        <div className="serveyform">
          <form
            onSubmit={this.props.handleSubmit(e =>
              axios.post("/api/forgot", { email: e.email }).then(msg => {
                this.setState({ msg: msg.data.message });

                e.email = "";
              })
            )}
          >
            <label> Email</label>
            <Field type="text" name="email" component="input" />

            <button type="submit">Submit</button>
          </form>
        </div>
        {this.state.msg ? (
          <span>
            <h4>{this.state.msg}</h4>{" "}
          </span>
        ) : (
          ""
        )}
      </div>
    );
  }
}
Forgot = connect(null, actions)(Forgot);
export default reduxForm({
  form: "forgot"
})(Forgot);



