import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import * as actions from "../actions";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

class Register extends Component {

  render() {
    return (
      <div className="serveyform">
        <div className="login">

          <form
            onSubmit={this.props.handleSubmit(e => {
              this.props.register(e.email, e.password,e.name);
              e.email = "";
              e.password = "";
              e.name = "";

            })}
          >
            <label> Name</label>
            <Field type="text" name="name" component="input" />
            <label> Email</label>
            <Field type="text" name="email" component="input" />
            <label> Password</label>
            <Field type="password" name="password" component="input" />
            <span className="butsubmit">
              <button className="logbut" type="submit">
                Submit
              </button>
              <Link to="/forgot" className="logbut">

                Forgot Password
              </Link>
            </span>
          </form>
        </div>
      </div>
    );
  }
}
Register = connect(null, actions)(Register);
export default reduxForm({
  form: "register"
})(Register);



